import uniCard from "@/components/uni-card/uni-card.vue";
import cmdCellItem from '@/components/cmd-cell-item/cmd-cell-item.vue'
import icard from "../../static/dist/card/index.js";
import {getAllRole,userProjectDepartmentQuery,departmentQuery} from "../../static/utils/api.js";

const login = require('../../static/utils/utils').Login;
const query = require('../../static/utils/utils').Query;
const Query = new query();
const Login = new login();

import {addFormId} from"../../static/utils/utils.js";
var _this;
export default {
	components: {uniCard,icard,cmdCellItem},
	data() {
		return {
			userInfo:{},
		    roleId:"",
			projectId:"",
			isRoot:"" ,         //特别设置的超级权限用户.
			roleName:"",        //权限对应的名称
			allRole:[]         ,//存放获取到的所有的专业
			
			departmentName:"",   //用户专业的名称
		}
	},

	//进行登录的更新
	onShow(){
	  _this = this
	  wx.getStorage({
	  	key:"userInfo",
		success:(res)=>{
		   let id = {
			   id:res.data.id
		   }
		   Query.findUser(id)
		   .then(data=>{
			   //console.log("返回的用户的信息",data.data.records[0]) 
			   _this.userInfo = data.data.records[0];
			   let isRoot =  data.data.records[0].isRoot;
			   if(isRoot){
				   _this.roleId = 0;
				   _this.roleName = "管理员"
			   }else{
				   uni.getStorage({
				   	key:"nowInProject",
					success:(res)=>{
						
		 				//设置普通权限并且,获取对应权限用户权限的名称和专业名称
						_this.projectId = res.data.projectId;
						_this.roleId = res.data.roleId;
						_this.getRoleName();
						if(_this.roleId ===2 ){
						  _this.getUserDepartmentId();	
						}
					 }  
				   })
			   }
		   })
		},
		fail:()=>{
		  uni.redirectTo({
		  	url:'../login/login'
		  })
		}
	  })
	},
	
	methods: {
		//获取对应权限的名称
		getRoleName:function(){
			_this = this;
			uni.request({
				url:getAllRole,
				method:'GET',
				dataType:'json'
			})
			.then(data=>{
				//根据查询到的权限的名臣对roleName进行赋值
				let allRole = data[1].data.data;
				_this.allRole = allRole;
				allRole.forEach((item,index)=>{
					if(item.id === _this.roleId){ 
						_this.roleName = item.name;
					}
				});
			})
			.catch(error=>{
				uni.showToast({
					icon:"none",
					duration:1000,
					title:"获取失败"
				})
			})
		},
		
		//根据用户的projectId以及用户的userId查找专业
		getUserDepartmentId:function(){
			_this = this;
			// console.log("查询的条件为",{
			// 				projectId:_this.projectId,
			// 				userId:_this.userInfo.id
			// 			})
			uni.showLoading({
				title:"切换中",
				success:()=>{
						uni.request({
						url:userProjectDepartmentQuery,
						method:"POST",
						data:{
							projectId:_this.projectId,
							userId:_this.userInfo.id
						},
						dataType:'json'
					 })
					.then(data=>{
						uni.hideLoading();
						console.log(data)
						if(data[1].data.data.records[0]){
							_this.getUserDepartmentName(data[1].data.data.records[0].departmentId)
						}else{
							_this.departmentName = "";
						}
					})
					.catch(Error=>{
						uni.showToast({
							title:"网络错误",
							icon:"none",
							duration:1000
						})
					})
				}
			})
		},
		
		//根据专业的id进行查询专业的名称
		getUserDepartmentName:function(departmentId){
			uni.showLoading({
			   title:"切换中",
			   success:()=>{
				   uni.request({
				   	  url:departmentQuery,
					  method:"POST",
					  data:{
						id:departmentId 
					  },
					  dataType:'json'
				   })
				   .then(data=>{ 
					   uni.hideLoading()
					   _this.departmentName = data[1].data.data.records[0].name
				   })
				   .catch(Error=>{
					   uni.showToast({
					   	 title:"网络错误",
						 duration:1000,
						 icon:"none"
					   })
				   })
			   }
			})
		},
		
		
		//退出登录
		logout:function(){
		   uni.removeStorage({
		   	 key:'userInfo',
			 success:()=>{
				uni.redirectTo({
					url:'../login/login'
				})
			 }
		   })
		},
		
		//向服务器增加自己的formId
		addMyFormId:function(e){
			_this = this;
			addFormId(_this.userInfo.openId,e.detail.formId);
		}
		
	}
}
